<h1>Tooltip 文字提示</h1>
<p>简单的文字提示气泡框</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>鼠标移入则显示提示，移出消失，气泡浮层不承载复杂文本和操作。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div class=\&quot;box\&quot;&gt;\n        &lt;div class=\&quot;top\&quot;&gt;\n            &lt;v-button v-tooltip.topleft=\&quot;msg\&quot;&gt;上左&lt;/v-button&gt;\n            &lt;v-button v-tooltip.top=\&quot;msg\&quot;&gt;上边&lt;/v-button&gt;\n            &lt;v-button v-tooltip.topright=\&quot;msg\&quot;&gt;上右&lt;/v-button&gt;\n        &lt;/div&gt;\n        &lt;div class=\&quot;left\&quot;&gt;\n            &lt;v-button v-tooltip.lefttop=\&quot;msg\&quot;&gt;左上&lt;/v-button&gt;\n            &lt;v-button v-tooltip.left=\&quot;msg\&quot;&gt;左边&lt;/v-button&gt;\n            &lt;v-button v-tooltip.leftbottom=\&quot;msg\&quot;&gt;左下&lt;/v-button&gt;\n        &lt;/div&gt;\n        &lt;div class=\&quot;right\&quot;&gt;\n            &lt;v-button v-tooltip.righttop=\&quot;msg\&quot;&gt;右上&lt;/v-button&gt;\n            &lt;v-button v-tooltip.right=\&quot;msg\&quot;&gt;右边&lt;/v-button&gt;\n            &lt;v-button v-tooltip.rightbottom=\&quot;msg\&quot;&gt;右下&lt;/v-button&gt;\n        &lt;/div&gt;\n        &lt;div class=\&quot;bottom\&quot;&gt;\n            &lt;v-button v-tooltip.bottomleft=\&quot;msg\&quot;&gt;下左&lt;/v-button&gt;\n            &lt;v-button v-tooltip.bottom=\&quot;msg\&quot;&gt;下边&lt;/v-button&gt;\n            &lt;v-button v-tooltip.bottomright=\&quot;msg\&quot;&gt;下右&lt;/v-button&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n    export default{\n        data :function(){\n            return {\n                msg: '&lt;em&gt;hello world&lt;/em&gt;',\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div class="box">
        <div class="top">
            <v-button v-tooltip.topleft="msg">上左</v-button>
            <v-button v-tooltip.top="msg">上边</v-button>
            <v-button v-tooltip.topright="msg">上右</v-button>
        </div>
        <div class="left">
            <v-button v-tooltip.lefttop="msg">左上</v-button>
            <v-button v-tooltip.left="msg">左边</v-button>
            <v-button v-tooltip.leftbottom="msg">左下</v-button>
        </div>
        <div class="right">
            <v-button v-tooltip.righttop="msg">右上</v-button>
            <v-button v-tooltip.right="msg">右边</v-button>
            <v-button v-tooltip.rightbottom="msg">右下</v-button>
        </div>
        <div class="bottom">
            <v-button v-tooltip.bottomleft="msg">下左</v-button>
            <v-button v-tooltip.bottom="msg">下边</v-button>
            <v-button v-tooltip.bottomright="msg">下右</v-button>
        </div>
    </div>
</template>

</div>
