    .bottom {
        clear: both;
        margin-left: 125px;
    }
    .left button, .right button {
        margin-bottom: 5px;
    }
</style>
<script>
    export default{
        data :function(){
            return {
                msg: '<em>hello world</em>',
                msg1: '请正确输入',
                msg2: '不保持显示',
                msg3: '保持显示',
            }
        }
    }
</script>


